.view .view-content {
  display: flex;
  flex-wrap: wrap;
}

.view .views-row {
  flex-basis: 20%;
  flex-grow: 1;
  margin: 0em 0.5em 0.5em 0em;
  flex-basis: 200px;
  background-color: white;
  transition: all 0.2s;
  position: relative;
}

.view img {
  object-fit: cover;
  object-position: center;
  display: block;
  transition: filter 0.2s;
  overflow: hidden;
}

.view img:hover {
  filter: brightness(0.9);
}

.view .views-field-title {
  line-height: normal;
  padding: 0.3em 0;
  font-size: 0.95em;
  flex-grow: 1;
}

.view .views-field-title a {
  text-decoration: none;
  transition: color 0.2s;
  color: #333;
  display: block;
}
